// __tests__/components/exercise-common/CategorySelector.test.js
import React from 'react';
import { render, fireEvent, act } from '@testing-library/react-native';
import CategorySelector, { areEqual } from '../../../src/components/exercise-common/CategorySelector';

// âœ… Mocks fiables et simplifiÃ©s pour les animations
const mockTiming = jest.fn(() => ({
  start: (callback) => {
    // ExÃ©cutez le callback de fin d'animation de maniÃ¨re synchrone
    // pour garantir la bonne exÃ©cution des tests.
    if (callback) {
      callback();
    }
  },
}));

const mockParallel = jest.fn(() => ({
  start: (callback) => {
    if (callback) {
      callback();
    }
  },
}));

// Les autres mocks restent les mÃªmes
const MockAnimatedValue = jest.fn().mockImplementation((initialValue) => {
  const mockValue = {
    _value: initialValue,
    interpolate: jest.fn(() => ({ _nativeTag: 1 })),
    setValue: jest.fn(),
    addListener: jest.fn(),
    removeListener: jest.fn(),
    removeAllListeners: jest.fn(),
    stopAnimation: jest.fn(),
    resetAnimation: jest.fn(),
  };
  return mockValue;
});

const mockMultiply = jest.fn((anim1, anim2) => ({
  _nativeTag: 1,
  interpolate: jest.fn(() => ({ _nativeTag: 1 })),
}));


jest.mock('react-native', () => {
  const RN = jest.requireActual('react-native');
  return {
    ...RN,
    Animated: {
      ...RN.Animated,
      Value: MockAnimatedValue,
      timing: mockTiming,
      parallel: mockParallel,
      multiply: mockMultiply,
      View: RN.Animated.View,
    },
    ScrollView: RN.ScrollView,
    TouchableOpacity: RN.TouchableOpacity,
    Text: RN.Text,
    View: RN.View,
  };
});

jest.mock('expo-linear-gradient', () => ({
  LinearGradient: ({ children, ...props }) => {
    const { View } = require('react-native');
    return <View testID="linear-gradient" {...props}>{children}</View>;
  },
}));

jest.mock('../../../src/components/exercise-common/CategorySelector/style', () => {
  const { StyleSheet } = require('react-native');
  return () => StyleSheet.create({
    container: {},
    backgroundGradient: {},
    scrollView: {},
    scrollContent: {},
    categoryItemWrapper: {},
    categoryTouchable: {},
    selectedCategoryItem: {},
    selectedInner: {},
    selectedCategoryText: { color: '#FFFFFF' },
    sparkleContainer: {},
    sparkle: {},
    categoryItem: {},
    categoryInner: {},
    categoryText: {},
  });
});

const categories = [
  { id: '1', name: 'Grammar' },
  { id: '2', name: 'Vocabulary' },
  { id: '3', name: 'Reading' },
];

describe('CategorySelector', () => {
  beforeEach(() => {
    jest.clearAllMocks();
  });

  // âœ… Groupe de tests de rendu basique
  describe('Basic rendering', () => {
    it('should render all categories correctly', () => {
      const { getByText } = render(
        <CategorySelector categories={categories} onSelectCategory={() => {}} />
      );
      expect(getByText('Grammar')).toBeTruthy();
      expect(getByText('Vocabulary')).toBeTruthy();
      expect(getByText('Reading')).toBeTruthy();
    });

    it('should display sparkle for selected category', () => {
      const { getByText } = render(
        <CategorySelector categories={categories} selectedCategory="1" onSelectCategory={() => {}} />
      );
      expect(getByText('âœ¨')).toBeTruthy();
    });
  });

  // âœ… Groupe de tests d'interactions
  describe('User interactions', () => {
    it('should call onSelectCategory when category is pressed', () => {
      const onSelectCategory = jest.fn();
      const { getByText } = render(
        <CategorySelector categories={categories} onSelectCategory={onSelectCategory} />
      );
      fireEvent.press(getByText('Vocabulary'));
      expect(onSelectCategory).toHaveBeenCalledWith('2');
    });

    it('should not call onSelectCategory for already selected category', () => {
      const onSelectCategory = jest.fn();
      const { getByText } = render(
        <CategorySelector categories={categories} selectedCategory="1" onSelectCategory={onSelectCategory} />
      );
      fireEvent.press(getByText('Grammar'));
      expect(onSelectCategory).not.toHaveBeenCalled();
    });
  });

  // ðŸŽ¯ Tests d'animations
  describe('Animations', () => {
    it('should trigger parallel animations when selectedCategory changes', () => {
      const onSelectCategory = jest.fn();
      const { rerender } = render(
        <CategorySelector
          categories={categories}
          selectedCategory="1"
          onSelectCategory={onSelectCategory}
        />
      );
      mockTiming.mockClear();
      mockParallel.mockClear();
      
      act(() => {
        rerender(
          <CategorySelector
            categories={categories}
            selectedCategory="2"
            onSelectCategory={onSelectCategory}
          />
        );
      });
      
      expect(mockParallel).toHaveBeenCalledTimes(1);
      expect(mockTiming).toHaveBeenCalledTimes(2);
    });

    it('should prevent animation conflicts with isAnimatingRef', () => {
      const onSelectCategory = jest.fn();
      const { rerender } = render(
        <CategorySelector
          categories={categories}
          selectedCategory="1"
          onSelectCategory={onSelectCategory}
        />
      );

      mockParallel.mockClear();

      act(() => {
        rerender(
          <CategorySelector
            categories={categories}
            selectedCategory="2"
            onSelectCategory={onSelectCategory}
          />
        );
      });

      act(() => {
        rerender(
          <CategorySelector
            categories={categories}
            selectedCategory="3"
            onSelectCategory={onSelectCategory}
          />
        );
      });
      
      expect(mockParallel).toHaveBeenCalledTimes(1);
    });
  });

  // ðŸ”„ Tests de gestion dynamique des catÃ©gories
  describe('Dynamic category management', () => {
    it('should handle category addition without crashing', () => {
      const onSelectCategory = jest.fn();
      const { rerender } = render(
        <CategorySelector
          categories={categories}
          selectedCategory="1"
          onSelectCategory={onSelectCategory}
        />
      );
      act(() => {
        rerender(
          <CategorySelector
            categories={[...categories, { id: '4', name: 'Writing' }]}
            selectedCategory="1"
            onSelectCategory={onSelectCategory}
          />
        );
      });
      expect(() => {
        rerender(
          <CategorySelector
            categories={[...categories, { id: '4', name: 'Writing' }]}
            selectedCategory="1"
            onSelectCategory={onSelectCategory}
          />
        );
      }).not.toThrow();
    });

    it('should handle category removal without crashing', () => {
      const onSelectCategory = jest.fn();
      const { rerender } = render(
        <CategorySelector
          categories={categories}
          selectedCategory="1"
          onSelectCategory={onSelectCategory}
        />
      );
      act(() => {
        rerender(
          <CategorySelector
            categories={[categories[0]]}
            selectedCategory="1"
            onSelectCategory={onSelectCategory}
          />
        );
      });
      expect(() => {
        rerender(
          <CategorySelector
            categories={[categories[0]]}
            selectedCategory="1"
            onSelectCategory={onSelectCategory}
          />
        );
      }).not.toThrow();
    });
  });

  // âœ… Tests areEqual
  describe('areEqual memoization', () => {
    it('should return true for identical props', () => {
      const props = { selectedCategory: '1', primaryColor: '#000', categories: [{ id: '1', name: 'Cat1' }] };
      expect(areEqual(props, props)).toBe(true);
    });
  });
});